.contactMe {
  display: grid;
  place-content: center;
  text-align: center;
  width: calc(100vw - 3.3rem);
  position: relative;
}
.contactMe::after {
  z-index: 0;
  position: absolute;
  content: "";
  padding: 20vh;
  bottom: 10%;
  left: 10%;
  border: solid 10px #FFD200;
}
.contactMe::before {
  z-index: 1;
  position: absolute;
  content: "";
  padding: 20vh;
  bottom: 35%;
  right: 10%;
  border: solid 10px #FFD200;
}
.contactMe > svg:nth-of-type(1) {
  z-index: 4;
  position: absolute;
  bottom: 0px;
  right: -100%;
  width: 200%;
  height: 20%;
}
.contactMe > svg:nth-of-type(2) {
  z-index: 3;
  position: absolute;
  bottom: 0px;
  height: 36%;
  right: -80%;
  width: 200%;
}
.contactMe > svg:nth-of-type(3) {
  z-index: 2;
  position: absolute;
  bottom: 0px;
  height: 50%;
  right: -90%;
  width: 200%;
}
.contactMe > svg:nth-of-type(4) {
  z-index: 2;
  position: absolute;
  left: -10%;
}
.contactMe > svg:last-of-type {
  z-index: 1;
  position: absolute;
  transform: scaleX(-1);
  bottom: -10%;
}
.contactMe > section {
  text-align: center;
  display: grid;
  place-content: center;
  height: 100vh;
  position: relative;
}
.contactMe > section > h2 {
  font-size: 7rem;
  position: relative;
  margin: 0px;
  background-color: #f2f1f1;
  color: transparent;
  -webkit-text-stroke: 2px #0076C4;
  z-index: 2;
}
.contactMe > section > h2::after {
  position: absolute;
  content: "";
  padding: 2px 15px;
  background-color: #0076C4;
  bottom: 10%;
  animation: opacityChange 1.5s infinite;
}

@media screen and (max-width: 1700px) {
  .contactMe > section h2 {
    font-size: 5rem;
  }
  .contactMe > svg:nth-of-type(4) {
    left: -10%;
  }
}
@media screen and (max-width: 1400px) {
  .contactMe > section h2 {
    font-size: 3.75rem;
  }
  .contactMe > svg:nth-of-type(4) {
    left: -30%;
  }
}
@media screen and (max-width: 800px) {
  .contactMe > section {
    height: 100vh;
  }
  .contactMe > section h2 {
    margin-top: 0rem;
    font-size: 2.25rem;
  }
  .contactMe > section h2::after {
    padding: 2px 10px;
  }
  .contactMe > svg:nth-of-type(4) {
    left: -60%;
  }
}
@keyframes opacityChange {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

